import React, { useEffect ,Component} from 'react'
import {
    NavLink, Route, Redirect,Switch,
    withRouter
} from 'react-router-dom'
import './doc.css';

import routes from '../router.config'
import {RouteWithSubRoutes,PrivateRoute} from '../App'
const DocContent = (props) => {
    return <h1>
        { props.match.params.type }
    </h1>
}
export{DocContent};


const Doc = (props) => {
    console.log(props)
    useEffect(()=>{
        if(!localStorage.getItem('userinfo')){
            props.history.push({
                pathname:'/login',
                state:props.location
            })
        }
    },[])
    return (
        <div className='doc'>
            
            <div className="content">
                {/* 嵌套路由 */ }
                <Switch>
                    {routes[1].routes.map((route, i) => {
                        if (route.pri) {//根据配置是否检测登录
                            return (
                                <PrivateRoute  key={i} path={route.path} >
                                <route.component/>
                                </PrivateRoute>
                            )
                        } else {
                            return (<RouteWithSubRoutes  key={i} {...route} />)
                        }
                    })}
                    </Switch>
                <Redirect from='/doc' to='/doc/core' />
            </div>
            <ol>
                <li><NavLink to='/doc/core'>核心概念</NavLink></li>
                <li><NavLink to='/doc/guild'>高级指引</NavLink></li>
                <li><NavLink to='/doc/api'>API</NavLink></li>
                <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
            </ol>
           
        </div>
    )
}
export default withRouter(Doc);