import React,{Component} from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './pages/Header';
import Home from './pages/Home';
import Doc from './pages/Doc';
import Blog from './pages/Blog';
import Comm from './pages/Comm';
import Login from './pages/Login';
import DocContent from './pages/Doc'
import Tuto from './pages/Tuto'
function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com />
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}
function RouteWithSubRoutes(route) {
	return (
	  <Route
		path={route.path}
		render={props => (
		  // pass the sub-routes down to keep nesting
		  <route.component {...props} routes={route.routes} />
		)}
	  />
	);
}
export{PrivateRoute,RouteWithSubRoutes};
const App = () => {
	return <Router>
		<Header />
		<Switch>
		    <Route exact path='/' component={ Home } />
			  <PrivateRoute path='/doc' component={ Doc } /> 
 			  <Route path='/doc' component={ Doc } component={DocContent} /> 
			  <PrivateRoute path='/tuto' component={ Tuto } />
			  <Route path='/blog' component={ Blog } />
		    <Route path='/comm' component={ Comm } />
			  <Route path='/login' component={ Login } /> 
		</Switch>
	</Router>
}
 export default App;


